.App {
  text-align: center;
  background-color: rgb(188, 204, 218);
  border: 1px teal;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 107, 107);
}

.MyCard {
  justify-content: center;
}
td {
  color: rgb(0, 107, 107);
}
table {
  margin-right: 10%;
  margin-left: 10%;
  border-radius: 3%;
  border-collapse: separate;
}
